@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .container {
    @apply mx-auto max-w-7xl px-4 sm:px-6 lg:px-8
  }

  .read_more {
    @apply text-sky-800 border-b-[2px] border-sky-800 pb-1 group-hover:text-sky-600  group-hover:border-sky-600
  }
}

p, li {
  @apply mb-2;
}

li a:hover {
  color: aliceblue;
}

.read_all{
  @apply text-sky-700 px-4 py-2 uppercase text-sm font-semibold hover:text-sky-600 border-b-2 border-sky-700 hover:border-sky-600
}

.pagination li a{
  @apply flex items-center justify-center px-4 h-10 text-gray-900 hover:text-sky-600
}